<template>
  <gs-date-picker
    v-if="type === 'timestamp'"
    :value="valueCache"
    :disabled="disabled"
    type="date-time"
    @change="change"
  />
  <gs-radio-group
    v-else-if="type === 'boolean'"
    :value="valueCache"
    :disabled="disabled"
    @change="change"
  >
    <gs-radio :label="true" />
    <gs-radio :label="false" />
  </gs-radio-group>
  <gs-input-number
    v-else-if="isNumber"
    :value="valueCache"
    :disabled="disabled"
    @change="change"
  />
  <gs-input
    v-else
    :value="valueCache"
    :disabled="disabled"
    @change="change"
  />
</template>
<script>
import moment from 'moment';
import {NUMBER_TYPE} from 'src/utils';
export default {
  name: 'FieldsInput',
  props: {
    value: {
      type: [String, Number, Object, Array, Boolean],
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      numberType: NUMBER_TYPE
    };
  },
  computed: {
    isNumber() {
      return this.numberType.some(i => this.type.startsWith(i));
    },
    valueCache() {
      if (this.type === 'timestamp' && this.value !== '') {
        return moment(this.value);
      } else {
        return this.value;
      }
    }
  },
  methods: {
    change(val) {
      this.$emit('input', this.type === 'timestamp' ? val.valueOf() : val);
    }
  }
};
</script>
<style lang="scss" scoped>
.gs-input {
  width: 300px;
}
</style>




